import requestUtil from '@/utils/request'

// 获取收货地址列表
export const getAddressApi = () => {
  return requestUtil.get('/address/list')
}

// 获取默认地址
export const defaultAddApi = () => {
  return requestUtil.get('/address/defaultId')
}

// 删除收货地址
export const delAddApi = (addressId) => {
  return requestUtil.post('/address/remove', {
    addressId
  })
}

// 设置默认地址
export const setDefaultAddApi = (addressId) => {
  return requestUtil.post('/address/setDefault', {
    addressId
  })
}

// 获取省市地址 for 添加地址
export const getAreaListApi = () => {
  return requestUtil.get('/region/tree')
}

// 添加新地址
export const createAddApi = (obj) => {
  const {
    name,
    phone,
    provId,
    provLabel,
    cityId,
    cityLabel,
    regId,
    regLabel,
    detail
  } = obj
  return requestUtil({
    method: 'post',
    url: '/address/add',
    data: {
      form: {
        name,
        phone,
        region: [
          {
            value: provId,
            label: provLabel
          },
          {
            value: cityId,
            label: cityLabel
          },
          {
            value: regId,
            label: regLabel
          }
        ],
        detail
      }
    }
  })
}

// 编辑地址-1.获取地址详情
export const getAddDetailApi = (addressId) => {
  return requestUtil({
    url: '/address/detail',
    params: {
      addressId
    }
  }
  )
}

// 编辑地址-2.提交修改后的地址详情
export const postNewAdd = (obj) => {
  const {
    addressId,
    name,
    phone,
    provId,
    provLabel,
    cityId,
    cityLabel,
    regId,
    regLabel,
    detail
  } = obj
  return requestUtil({
    method: 'post',
    url: '/address/edit',
    data: {
      addressId,
      form: {
        name,
        phone,
        region: [
          {
            value: provId,
            label: provLabel
          },
          {
            value: cityId,
            label: cityLabel
          },
          {
            value: regId,
            label: regLabel
          }
        ],
        detail
      }
    }
  })
}
