<template>
  <!-- 搜索框 -->
  <van-search
    readonly
    shape="round"
    background="#f1f1f2"
    :placeholder="recmdItem_banner"
    @click="goSearchIndex"
  />
</template>

<script>
export default {
  name: 'SearchBanner',

  created () {
    // console.log('created执行')
    this.showRecommand()
  },

  activated () {
    if (!this.timer) {
      // console.log('activated执行')
      this.showRecommand()
    }
  },

  deactivated () {
    clearInterval(this.timer)
    this.timer = null
  },

  destroyed () {
    clearInterval(this.timer)
  },

  data () {
    return {
      recommandList: this.$store.state.search.recommandList,
      index: 0,
      recmdItem_banner: '',
      timer: null
    }
  },
  computed: {

  },

  methods: {
    showRecommand () {
      // 初始化
      this.recmdItem_banner = this.$store.state.search.recommandList[0]
      this.$store.commit('search/changeRecmdItem', this.recmdItem_banner)
      // 循环list
      this.timer = setInterval(() => {
        this.index++
        // 从头开始
        if (this.index > this.recommandList.length - 1) {
          this.index = 0
        }
        this.recmdItem_banner = this.recommandList[this.index]
        // seachBanner的recmdItem变化同步到vuex的recmdItem
        this.$store.commit('search/changeRecmdItem', this.recmdItem_banner)
        console.log(this.timer)
      }, 2000)
    },

    goSearchIndex () {
      this.$router.push('/search')
      // searchBanner -> searchIndex 搜索框默认为空
      this.$store.commit('search/setIndexValue', '')
    }

  }
}
</script>

<style lang="less" scoped>
.van-search {
  position: fixed;
  width: 100%;
  top: 46px;
  z-index: 999;
}
</style>
