<template>
  <div class="home">
    <!-- 导航条 -->
    <van-nav-bar title="在线商城" fixed />

    <SearchBanner></SearchBanner>

    <!-- 轮播图 -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item
        v-for="(item, index) in swipeList"
        :key="index"
      >
        <img :src="item.imgUrl" alt="" @click="$router.push('/category')" />
      </van-swipe-item>
    </van-swipe>

    <!-- 导航 -->
    <van-grid column-num="5" icon-size="40">
      <van-grid-item
        v-for="(item, index) in gridList"
        :key="index"
        :icon="item.imgUrl"
        :text="item.text"
        @click="$router.push('/category')"
      />
    </van-grid>

    <!-- 主会场 -->
    <div class="main">
      <img src="@/assets/main.png" alt="" @click="$router.push('/category')" />
    </div>

    <!-- 猜你喜欢 -->
    <div class="guess">
      <p class="guess-title">—— 猜你喜欢 ——</p>

      <div class="goods-list">
        <GoodsItem
          v-for="(item, index) in guessList"
          :key="index"
          :item="item"
        ></GoodsItem>
      </div>
    </div>
  </div>
</template>

<script>
import GoodsItem from '@/components/GoodsItem.vue'
import SearchBanner from '@/components/SearchBanner'

import { getHomeDetailApi } from '@/api/home'

export default {
  name: 'HomePage',
  components: {
    GoodsItem,
    SearchBanner
  },

  data () {
    return {
      swipeList: [],
      gridList: [],
      guessList: []
    }
  },

  async created () {
    console.log('created触发')
    this.getHomeDetail()
  },

  activated () {
    console.log('activated触发')
  },

  methods: {
    async getHomeDetail () {
      const { data: { pageData: { items } } } = await getHomeDetailApi()
      this.swipeList = items[1].data
      this.gridList = items[3].data
      this.guessList = items[6].data
    }
  }
}
</script>

<style lang="less" scoped>
// 主题 padding
.home {
  padding-top: 100px;
  padding-bottom: 50px;
}

// 导航条样式定制
.van-nav-bar {
  z-index: 999;
  background-color: #c21401;
  ::v-deep .van-nav-bar__title {
    color: #fff;
  }
}

// 分类导航部分
.my-swipe .van-swipe-item {
  height: 185px;
  color: #fff;
  font-size: 20px;
  text-align: center;
  background-color: #39a9ed;
}
.my-swipe .van-swipe-item img {
  width: 100%;
  height: 185px;
}

// 主会场
.main img {
  display: block;
  width: 100%;
}

// 猜你喜欢
.guess .guess-title {
  height: 40px;
  line-height: 40px;
  text-align: center;
}

// 商品样式
.goods-list {
  background-color: #f6f6f6;
}
</style>
