import { getCartListApi, UpdateGoodsApi, toDelGoodsApi } from '@/api/cart'
import { Toast, Dialog } from 'vant'

export default {
  namespaced: true,

  state () {
    return {
      cartList: []
    }
  },

  getters: {
    // 商品总数
    totalGoods (state) {
      return state.cartList.reduce((sum, item) => sum + item.goods_num, 0)
    },

    // 选中的商品列表
    checkedGoods (state) {
      return state.cartList.filter((item) => item.isChecked === true)
    },

    // 选中的商品的总数量
    checkedGoodsNum (state, getters) {
      return getters.checkedGoods.reduce((sum, item) => sum + item.goods_num, 0)
    },

    // 选中的商品的总价格
    checkedGoodsPrice (state, getters) {
      return getters.checkedGoods.reduce((sum, item) => sum + item.goods_num * item.goods.goods_price_min, 0).toFixed(2)
    },

    isAllChecked (state) {
      return state.cartList.every(item => item.isChecked === true)
    },

    checkedGoodsIds (state, getters) {
      // 获得选中商品的列表
      const arr = getters.checkedGoods
      return arr.map(item => item.id)
    },

    confirmMsg (state, getters) {
      const arr = getters.checkedGoods
      return `确定要删除这${arr.length}件宝贝吗`
    }

  },

  mutations: {
    setCartList (state, arr) {
      state.cartList = arr
    },

    toggleChecked (state, id) {
      const obj = state.cartList.find(item => item.goods_id === id)
      obj.isChecked = !obj.isChecked
    },

    // 全选 取消反选
    checkAll (state, flag) {
      state.cartList.forEach(item => { item.isChecked = !flag })
    },

    // 本地-更改商品数量
    changeCountLocal (state, obj) {
      const { goodsId, goodsNum } = obj
      const target = state.cartList.find(item => item.goods_id === goodsId)
      target.goods_num = goodsNum
    }
  },

  actions: {
    // 初始化购物车
    async readCartList (context) {
      const { data: { list } } = await getCartListApi()

      list.forEach(element => {
        element.isChecked = false
      })
      context.commit('setCartList', list)
    },

    // 后端-更改商品数量
    async changeCount (context, obj) {
      // console.log(obj)
      const { goodsId, goodsNum, goodsSkuId } = obj
      context.commit('changeCountLocal', obj)

      await UpdateGoodsApi(goodsId, goodsNum, goodsSkuId)
    },

    // 删除商品
    toDelGoods (context) {
      if (!context.getters.checkedGoodsNum) {
        Toast('还没有选择宝贝哦')
        return
      }
      Dialog.confirm({
        message: context.getters.confirmMsg
      })
        .then(async () => {
          await toDelGoodsApi(context.getters.checkedGoodsIds)
          context.dispatch('readCartList')
        })
        .catch(() => { })
    }
  }

}
