import {
  getAddressApi, defaultAddApi, delAddApi, setDefaultAddApi, createAddApi

} from '@/api/address'

export default {
  namespaced: true,

  state () {
    return {
      addressList: [],
      defaultAddId: 0,
      chosenAddId: 0
    }
  },

  getters: {},

  mutations: {
    // 修改默认地址ID
    setDefaultId (state, addressId) {
      state.defaultAddId = addressId
    },

    // 为地址列表赋值
    setAddList (state, list) {
      state.addressList = list
    },

    // 选择新的收货地址
    setChosenAdd (state, arg) {
      state.chosenAddId = arg
    }
  },

  actions: {
    // 获取地址列表
    async getAddress (context) {
      const { data: { list } } = await getAddressApi()
      context.commit('setAddList', list)
    },

    // 获取默认地址
    async getDefaultAdd (context) {
      const { data: { defaultId } } = await defaultAddApi()
      context.commit('setDefaultId', defaultId)
    },

    // 删除地址
    async delAddress (context, addressId) {
      await delAddApi(addressId)
      // 如果删除的是结算页面中的地址
      if (addressId === context.state.chosenAddId) {
        context.state.chosenAddId = 0
      }
      context.dispatch('getAddress', 'getDefaultAdd')
    },

    // 设置默认地址
    async setDefaultAdd (context, addressId) {
      await setDefaultAddApi(addressId)
      context.dispatch('getDefaultAdd')
    },

    // 添加新地址
    async createAdd (context, obj) {
      await createAddApi(obj)
      context.dispatch('getAddress', 'getDefaultAdd')
    }

  }

}
