import { getLocalInfo, setLocalInfo } from '@/utils/storage'

export default {
  namespaced: true,

  state () {
    return {
      userInfo: getLocalInfo()

    }
  },

  getters: {},

  mutations: {
    setUserInfo (state, obj) {
      state.userInfo = obj
      setLocalInfo(obj)
    }
  },

  actions: {
    userLogout (context) {
      context.commit('setUserInfo', {})
      context.commit('cart/setCartList', [], { root: true })
    }
  }
}
