import requestUtil from '@/utils/request'

// 添加商品到购物车
export const addToCartApi = (goodsId, goodsNum, goodsSkuId) => {
  return requestUtil({
    method: 'post',
    url: '/cart/add',
    data: {
      goodsId,
      goodsNum,
      goodsSkuId
    }
  })
}

// 购物车商品列表
export const getCartListApi = () => {
  return requestUtil.get('/cart/list')
}

// 购物车商品更新
export const UpdateGoodsApi = (goodsId, goodsNum, goodsSkuId) => {
  return requestUtil.post('/cart/update',
    {
      goodsId,
      goodsNum,
      goodsSkuId
    }
  )
}

// 购物车商品总数量
export const getCartTotalApi = () => {
  return requestUtil.get('/cart/total')
}

// 删除购物车商品
export const toDelGoodsApi = (cartIds) => {
  return requestUtil.post('/cart/clear', {
    cartIds
  })
}
