const INFO_KEY = 'shopvueKey'
const SEARCH_KEY = 'SearchHistory'

// 先查询本地是否有用户token
export const getLocalInfo = () => {
  const defaultKey = {
    token: '',
    userId: ''
  }
  const res = localStorage.getItem(INFO_KEY)
  // 如果本地存在则使用，不存在则初始为空
  return res ? JSON.parse(res) : defaultKey
}

// 将用户信息存储在本地
export const setLocalInfo = (obj) => {
  localStorage.setItem(INFO_KEY, JSON.stringify(obj))
}

// 获取搜索历史
export const getLocalSearchHistory = () => {
  const res = localStorage.getItem(SEARCH_KEY)
  return res ? JSON.parse(res) : []
}

// 将搜索历史存储在本地
export const setLocalSearchHistory = (obj) => {
  localStorage.setItem(SEARCH_KEY, JSON.stringify(obj))
}
